import { Component} from '@angular/core';
import { Platform } from 'ionic-angular';

import { Configuration } from '../../config/voj-configuration';
import { FormType } from '../../models/form-type.model';
import { VojPageHeaderType } from '../../models/voj-page-header.model';
import { VojConfiguration } from '../../models/voj-configuration.model';

@Component({
  selector: 'bank-settings-page',
  templateUrl: 'bank-settings.html',
})
export class BankSettingsPage {
  config: VojConfiguration;
  formData: FormType;
  headerData: VojPageHeaderType;
  isMobile: boolean = false;

  constructor(public configurationService: Configuration,
              public platform: Platform){
    this.config = this.configurationService.configuration;
    this.config.isLargeScreen ? this.isMobile = false : this.isMobile = true;

    this.headerData = {
      title: 'My bank details',
      style: 'any',
      actions: [{
        id: 'xdcfgvyhubijnklbhvg',
        label: 'help',
        icon: 'help',
        action: function () {
          console.log('button works')
        }
      }],
      isCanceled: true
    };

    this.formData = {
      fields: [
        {
          type: 'number',
          label: 'Bank name',
          readonly: false,
          visible: true,
          id: '',
          name: 'Bank name',
          required: true
        },
        {
          type: 'text',
          label: 'Name of the holder',
          readonly: false,
          visible: true,
          id: '',
          name: 'Name of the holder',
          required: true
        },
        {
          type: 'number',
          label: 'IBAN',
          readonly: false,
          visible: true,
          id: '',
          name: 'IBAN',
          required: true
        },
        {
          type: 'number',
          label: 'BIC',
          readonly: false,
          visible: true,
          id: '',
          name: 'BIC',
          required: true
        }
      ],
      submit: {
        id: 'xdcfgvyhubijnkfwlbhvg',
        label: 'Suivant',
        icon: 'help',
        action: function () {
          console.log('button works')
        }
      }
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad BankSettingsPage');
  }

}



