import {Component} from '@angular/core';

import { VojPageHeaderType } from '../../models/voj-page-header.model';
import { VojFormModel } from '../../models/voj-form.model';
import {AccountService} from "../../providers/account-service/account-service";
import {Storage} from "@ionic/storage";
import {ModalService} from "../../services/modal.service";
import {SignUpPage} from "../sign-up/sign-up";
import {ViewController} from "ionic-angular";
import {NotificationType} from "../../models/notification-type.model";
import {VojNotificationService} from "../../components/voj-notification/voj-notification.service";
import {CurrentUserDto} from "../../dtos/current-user.dto";
import {md5} from "../../scripts/md5";
import {MasterdataService} from "../../providers/masterdata-service/masterdata-service";
import {CountryDto} from "../../dtos/country.dto";
import {ResetPasswordPage} from "../reset-password/reset-password";

@Component({
    selector: 'login-page',
    templateUrl: 'login.html',
})
export class LoginPage {

    dataPageHeader: VojPageHeaderType;
    formData: VojFormModel;

    constructor(public accountService: AccountService,
                private storage: Storage,
                public modalService: ModalService,
                public viewCtrl: ViewController,
                public vojNotification: VojNotificationService,
								private masterdataService: MasterdataService) {
			// Set form data
			this.formData = {
					title: '',
					fields: [
						// Phone code
						{
							type: 'select',
							value: null,
							label: 'FORM.COUNTRY_CODE',
							dataset: [],
							required: true,
							visible: true,
							id: 'phone-code-field',
							name: 'code'
						},
						// Phone
							{
									placeholder: '',
									type: 'tel',
									value: '',
									label: 'FORM.PHONE',
									required: true,
									readonly: false,
									visible: true,
									min: 9,
									max: 10,
									regex: '^[0-9]*$',
									id: 'phone-field',
									name: 'phone'
							},
							// Password
							{
									placeholder: '',
									type: 'password',
									value: '',
									label: 'FORM.PASSWORD',
									required: true,
									readonly: false,
									visible: true,
									min: 6,
									max: 100,
									regex: '',
									id: 'password-field',
									name: 'password'
							},
							// Forgotten password button
							{
									type: 'button',
									label: 'LOGIN.FORGOTTEN_PASSWORD',
									visible: true,
									id: 'forgotten-password-btn',
									action: () => {
										this.viewCtrl.dismiss().then(() => {
											this.modalService.present(ResetPasswordPage, false);
										});
									}
							}
					],
					submit: {
							id: '',
							icon: '',
							label: 'LOGIN.VALIDATE',
							action: () => {
									console.log('Click validate button.');
									this.authenticate();
							}
					}
			};

			// Set page header
			this.dataPageHeader = {
				title: 'LOGIN.TITLE',
				actions: [
					{
						id: 'help',
						label: '',
						icon: 'help',
						action: () => {
							console.log('Click help button.')
						}
					}
				],
				style: 'any',
				isCanceled: true
			};
		}

		ngOnInit(){
			// load phone code list
			this.masterdataService.loadCountriesList().then((data: CountryDto[]) => {
				//set dataset of phone codes of the form
				this.formData.fields.filter(f => f.name === "code" )[0].dataset = this.masterdataService.convertCountriesListToDataset(data);
				//the default phone code is France code (33)
				this.formData.fields.filter(f => f.name === "code" )[0].value = "33";
			});
		}

    authenticate(){
			//prepare credentials data
			let code = this.formData.fields.filter(f => f.name === "code" )[0].value;
			let phone = this.formData.fields.filter(f => f.name === "phone" )[0].value;
			let password = md5(this.formData.fields.filter(f => f.name === "password" )[0].value);
			let phoneWithCode = "+" + code + phone.substr(phone.length - 9);

			//call web service method
			this.accountService.authenticate(phoneWithCode, password).then((data:any)=> {
            console.log(JSON.stringify(data));
            if(data.status == "passwordError") {
                let msg = 'Le mot de passe est incorrect. Veuillez réessayer.';
                this.displayErrorNotification(msg);
                return;
            }

            if(data.found == false){
							let msg = 'Aucun compte associé à ce numéro de téléphone.';
							this.displayErrorNotification(msg);
							return;
            }

            if(data.id != 0){
              let account: CurrentUserDto = data;
              this.storage.set("currentUser", JSON.stringify(account)).then(()=> {
                this.viewCtrl.dismiss();
              });
            }
          });
    }

    goToSignup() {
			this.viewCtrl.dismiss();
			this.modalService.present(SignUpPage);
		}

	displayErrorNotification(msg: string){
		let notificationData: NotificationType = {
			icon: 'star',
			title: 'Warning',
			message: msg,
			backdropDismiss: true,
			buttons: [
				{
					id: 'cancel',
					label: 'OK',
					icon: 'alert',
					action: () => {
						console.log('Cancel clicked');
					}
				}
			]
		};
		this.vojNotification.present(notificationData);
  }
}


