import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import {HttpRequestProvider} from "../http-request/http-request";
import {Configuration} from "../../config/voj-configuration";
import {VojTuple} from "../../models/voj-tuple.model";
import {SearchResultDTO} from "../../dtos/search-result.dto";
import {VojCardModel} from "../../models/voj-card.model";
import {Title} from "@angular/platform-browser";
import {TextType} from "../../models/text-type";
import {SearchQuery} from "../../models/search-query.model";


@Injectable()
export class SearchServiceProvider {

  idIndexation : number;
  results : Array<SearchResultDTO>;

  constructor(public http: HttpRequestProvider,
              public configuration : Configuration) {

  }

  loadDetails(id : number){
    let payload = {
      "service": "DETAILS",
      "role": this.configuration.configuration.customer == "J" ? "employer" : "jobyer",
      "selectedRow": id,
      "index": this.idIndexation
    };
    let url = "";
    for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
      let t : VojTuple = this.configuration.configuration.urls[i];
      if (t.key == "search") {
        url = t.value;
        break;
      }
    }

    return new Promise(resolve => {
      this.http.call(payload, url, "", true).subscribe((data:any)=>{
        console.log(JSON.stringify(data));
        resolve(data);
      });
    });
  }

  /**
   * Seek offers/jobyers based on composit query
   * @param SearchQuery object
   * @returns {Promise<T>}
   */
  criteriaSearch(query : SearchQuery) {
    let payload = {
      "service": "CRITERIA",
      "role": this.configuration.configuration.customer == "J" ? "employer" : "jobyer",
      "query": query
    };
    let url = "";
    for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
      let t : VojTuple = this.configuration.configuration.urls[i];
      if (t.key == "search") {
        url = t.value;
        break;
      }
    }
    return new Promise(resolve => {
      this.http.call(payload, url, "", true).subscribe((data:any)=>{
        this.results = [];
        let cards = [];
        for(let i = 0 ; i < data.length ; i++) {
          let r : SearchResultDTO = new SearchResultDTO(data[i]);
          let c = r.toVojCard();
          cards.push(c);
          this.results.push(r);
        }
        resolve(cards);
      });
    });
  }

  /**
   * Seek offers/jobyers based on natural language query
   * @param fullText
   * @returns {Promise<T>}
   */
  semanticSearch(fullText : string) {
    let payload = {
      "service": "FULL_TEXT",
      "role": this.configuration.configuration.customer == "J" ? "employer" : "jobyer",
      "fullText": fullText
    };

    let url = "";
    for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
      let t : VojTuple = this.configuration.configuration.urls[i];
      if (t.key == "search") {
        url = t.value;
        break;
      }
    }
    return new Promise(resolve => {
      this.http.call(payload, url, "", true).subscribe((data:any)=>{
        this.results = [];
        let cards = [];
        for(let i = 0 ; i < data.length ; i++) {
          let r : SearchResultDTO = new SearchResultDTO(data[i]);
          let c = r.toVojCard();
          cards.push(c);
          this.results.push(r);
        }
        resolve(cards);
      });
    });
  }

}



