import {Injectable} from '@angular/core';
import {Events} from "ionic-angular";
import {VojCardModel} from "../../models/voj-card.model";
import {eBusType, Passenger} from "../../models/voj-event.model";

/*
  Generated class for the EventBusProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class EventBusProvider {

    //detailsCardPage: DetailsPage;
    passenger:Passenger;

    constructor(private event: Events) {
        console.log('Hello EventBusProvider Provider');
        this.passenger = new Passenger();
    }

    // MAP EVENTS
    /**
     * Send an event to set map values
     * @param {eMapType} passenger
     */
    set(passenger: eBusType) {
        if (passenger.condition)
            this.event.publish(passenger.topic, passenger.data);
    }

    /**
     * Get an event to load map values
     */
    get(passenger: eBusType) {
        return new Promise((resolve, reject) => {
            this.event.subscribe(passenger.topic, (data) => {
                if (passenger.condition)
                    resolve(data);
                else
                    reject('Passenger type is rejected for this bus line');
            });
        });
    }

    /*subscribeDetailsPage(instance: DetailsPage) {
        this.detailsCardPage = instance;
    }

    publishCardDetails(card: VojCardModel) {
        this.detailsCardPage.updateCard(card);
    }

    setDetailsView(data: { isVisible: boolean, id: number }) {
        this.event.publish('SHOW_DETAILS', data);
    }

    getDetailsView() {
        return new Promise((resolve) => {
            this.event.subscribe('SHOW_DETAILS', (data: { isVisible: boolean, id: number }) => {
                resolve(data);
            });
        });
    }
     */

}



