import {Component} from '@angular/core';
import {IonicPage, Platform} from 'ionic-angular';
import {FieldType} from '../../models/field-type';
import {VojPageHeaderType} from '../../models/voj-page-header.model';
import {ButtonType} from "../../models/button-type.model";
import {MasterdataService} from "../../providers/masterdata-service/masterdata-service";
import {NationalityDto} from "../../dtos/nationality.dto";

@IonicPage()
@Component({
    selector: 'personal-details-page',
    templateUrl: 'personal-details.html',
})
export class PersonalDetailsPage {

    isMobile: boolean;
    headerData: VojPageHeaderType;
    formData: {
        fields: Array<FieldType>;
        submit: ButtonType;
    };

    constructor(public platform: Platform,
                private masterdataService: MasterdataService) {
        platform.is('core') ? this.isMobile = false : this.isMobile = true;
        this.headerData = {
            title: 'My personal data 1/2',
            style: 'any',
            isCanceled: true,
            actions: [{
                id: 'xdfgchvbjknm',
                label: 'help',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }],

        };

        this.formData = {
            fields: [
                {
                    required: true,
                    type: 'date',
                    label: 'Date of birth',
                    readonly: false,
                    visible: true,
                    id: 'date-of-birth',
                    validationMsg: 'Date of birth error',
                    name: 'dateOfBirth'
                },
                {
                    required: true,
                    type: 'select',
                    value: 'Country of birth',
                    label: 'Country of birth',
                    dataset: [
                        {id: 1, key: 'france', value: 'France'},
                        {id: 2, key: 'england', value: 'England'},
                        {id: 3, key: 'russia', value: 'Russia'},

                    ],
                    visible: true,
                    validationMsg: 'Country of birth error',
                    id: 'country-of-birth',
                    name: 'countryBirth'
                },
                {
                    required: true,
                    type: 'select',
                    value: 'Department of birth',
                    label: 'Department of birth',
                    dataset: [
                        {id: 1, key: 'test1', value: '92 - test'},
                        {id: 2, key: 'test2', value: '101 - Some text'},
                    ],
                    readonly: false,
                    visible: true,
                    validationMsg: 'Department of birth error',
                    id: 'department-of-birth',
                    name: 'departmentOfBirth'
                },
                {
                    required: true,
                    type: 'text',
                    label: 'Place of birth',
                    readonly: false,
                    visible: true,
                    validationMsg: 'Place of birth error',
                    id: 'place-of-birth',
                    name: 'placeOfBirth'
                },
                {
                    required: true,
                    type: 'number',
                    value: 'Social Security Number',
                    label: 'Social Security Number',
                    readonly: false,
                    visible: true,
                    validationMsg: 'Social security number error',
                    id: 'social-security-number',
                    name: 'socialSecurityNumber'
                }
            ],
            submit: {
                label: 'Valider',
                id: 'valid-btn',
                icon: 'no',
                action: Function
            }
        }
    }

	ngOnInit(){
		//load nationality list
		this.masterdataService.loadNationalitiesList().then((data: NationalityDto[]) => {
			//set dataset of nationalities of the form
			this.formData.fields.filter(f => f.name === "countryBirth" )[0].dataset = this.masterdataService.convertNationalitiesListToDataset(data);
			//the default nationality is "France"
			this.formData.fields.filter(f => f.name === "countryBirth" )[0].value = "France";
		});
	}
}



