import { ModalController } from 'ionic-angular'
import { Injectable } from '@angular/core';

import { Configuration } from '../config/voj-configuration';
import { SignUpPage } from '../pages/sign-up/sign-up';
import { SignUpType } from '../models/sign-up-type.model';
import {Page} from "ionic-angular/navigation/nav-util";

@Injectable()
export class ModalService {

    constructor(
        public configurationService: Configuration,
        public modalCtrl: ModalController
    ) {}

    // header - optional parameter. If false - without header
    present(page: Page, header?: boolean): void {
        console.log('header:', header);
        // Options for modal window
        let modalOptions = {
            cssClass: 'voj-modal',
            //enableBackdropDismiss: signUpData.backdropDismiss
            enableBackdropDismiss: true
        };

        // Create modal window
        let modal = this.modalCtrl.create(page, {}, modalOptions);

        // Show modal window
        modal.present().then(() => {
            // Call on mobile screen
            if (this.configurationService.configuration.isLargeScreen === true) {
                this.setModalHeight(header);
            }
        });
    }

    dismiss() {

    }

    setModalHeight(header): void {
        console.log('setModalHeight');
        // Content of page
        let vojContents = document.getElementsByClassName('voj-content');
        let modalContent = vojContents[vojContents.length - 1];
        // Modal window
        let modalWrapper = document.getElementsByClassName('modal-wrapper')[0];

        // Set height and show modal
        (modalWrapper as HTMLElement).style.display = 'block';
        let modalHeader = (header === false) ? 0 : 56;
        (modalWrapper as HTMLElement).style.height = modalContent.clientHeight + modalHeader + 'px';
    }

}



