import {Configuration} from "../../config/voj-configuration";
import {VojConfiguration} from "../../models/voj-configuration.model";
import {VojTuple} from "../../models/voj-tuple.model";
import {HttpRequestProvider} from "../http-request/http-request";
import {CountryDto} from "../../dtos/country.dto";
import {Injectable} from "@angular/core";
import {NationalityDto} from "../../dtos/nationality.dto";

@Injectable()
export class MasterdataService{
	config: VojConfiguration;

	constructor(private configuration: Configuration,
							private http: HttpRequestProvider){
		this.config = this.configuration.configuration;
	}

	loadCountriesList() {
		let payload = {
			"type": "COUNTRIES",
		};
		let url = "";
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "masterdata") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: CountryDto[]) => {
				console.log(JSON.stringify(data));
				resolve(data);
			});
		});
	}

	loadNationalitiesList() {
		let payload = {
			"type": "NATIONALITIES",
		};
		let url = "";
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "masterdata") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: NationalityDto[]) => {
				console.log(JSON.stringify(data));
				resolve(data);
			});
		});
	}

	loadDepartmentList() {
		let payload = {
			"type": "DEPARTMENTS",
		};
		let url = "";
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "masterdata") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: NationalityDto[]) => {
				console.log(JSON.stringify(data));
				resolve(data);
			});
		});
	}

	loadPrefectureList(){
		let payload = {
			"service": "PREFECTURES"
		};
		//let url = (this.config.urls.filter(tuple => tuple.key === "identity")).value;
		let url;
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "profile") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			//TODO: create the PrefectureDTO class. this Promise should return an array of PrefectureDTO
			this.http.call(payload, url, "", true).subscribe((data: any) => {
				console.log(JSON.stringify(data));
				resolve(data);
			});
		});
	}

	convertCountriesListToDataset(countries: CountryDto[]){
		let dataset: Array<{ id: number, key: string, value: string }> = [];
		for(let i = 0; i < countries.length; i++){
			let ds = {id: countries[i].gid, key: countries[i].name + "(" + countries[i].phoneCode + ")", value: countries[i].phoneCode};
			dataset.push(ds);
		}
		return dataset;
	}

	convertNationalitiesListToDataset(nationalities: NationalityDto[]){
		let dataset: Array<{ id: number, key: string, value: string }> = [];
		for(let i = 0; i < nationalities.length; i++){
			let ds = {id: nationalities[i].gid, key: nationalities[i].name , value: nationalities[i].gid.toString()};
			dataset.push(ds);
		}
		return dataset;
	}
}


