import { Component } from '@angular/core';

import { VojPageHeaderType } from '../../models/voj-page-header.model';
import { VojFormModel } from '../../models/voj-form.model';
import {VojConfiguration} from "../../models/voj-configuration.model";
import {Configuration} from "../../config/voj-configuration";
import {AccountService} from "../../providers/account-service/account-service";
import {ViewController} from "ionic-angular";
import {CurrentUserDto} from "../../dtos/current-user.dto";
import {Storage} from "@ionic/storage";
import {ModalService} from "../../services/modal.service";
import {MainAddressPage} from "../main-address/main-address";
import {IdentityDto} from "../../dtos/identity.dto";
import {StatusResponseDto} from "../../dtos/status-response.dto";
import {VojNotificationService} from "../../components/voj-notification/voj-notification.service";
import {NotificationType} from "../../models/notification-type.model";

@Component({
    selector: 'identity-page',
    templateUrl: 'identity.html',
})
export class IdentityPage {

    dataPageHeader: VojPageHeaderType;
    formData: VojFormModel;
    config: VojConfiguration;
    currentUser: CurrentUserDto;

    constructor(public configurationService: Configuration,
                public accountService: AccountService,
                public viewCtrl: ViewController,
                private storage: Storage,
                public modalService: ModalService,
								private notification: VojNotificationService) {
        this.config = this.configurationService.configuration;
        // Set page header
        this.dataPageHeader = {
            title: 'Mon identité',
            actions: [
                {
                    id: 'help',
                    label: '',
                    icon: 'help',
                    action: () => {
                        console.log('Click help button.')
                    }
                }
            ],
            style: 'any',
					  isCanceled: true
        };

        	// Set form data
					this.formData = {
						title: '',
						fields: [
							// Radio for civility
							{
								type: 'radio',
								value: "",
								label: 'Civilité',
								dataset: [
									{id: 1, key: 'Mme', value: 'Mme'},
									{id: 2, key: 'Mr', value: 'Mr'}
								],
								required: true,
								visible: true,
                    id: '',
								name: 'civility',
							},
							// Text for name
							{
								placeholder: '',
								type: 'text',
								value: "",
								label: 'Name',
								required: true,
								readonly: false,
								visible: true,
								min: 0,
								max: 100,
								regex: '^[A-Z]*$',
                    id: '',
								name: 'lastName'
							},
							// Text for maiden name
							{
								placeholder: '',
								type: 'text',
								value: "",
								label: 'Maiden name',
								required: true,
								readonly: false,
								visible: true,
								min: 0,
								max: 100,
								regex: '^[A-Z]*$',
                    id: '',
								name: 'middleName'
							},
							// Text for Prénom
							{
								placeholder: '',
								type: 'text',
								value: "",
								label: 'Prénom',
								required: true,
								readonly: false,
								visible: true,
								min: 0,
								max: 100,
								regex: '^[A-Z][a-z]*$',
                    id: '',
								name: 'firstName'
							}
						],
						submit: {
							id: '',
							icon: '',
							label: 'Suivant',
							action: (event) => {
								console.log('Present Information Resume page.')
								this.saveIdentity();

							}
						}
					};
    }

    ngOnInit(){
			this.storage.get("currentUser").then((data: string) => {
				if (!data) {
					return;
				}
				this.currentUser = JSON.parse(data);

				// Set form data
				this.formData.fields.filter(f => f.name === "civility" )[0].value = this.currentUser.identity.civility;
				this.formData.fields.filter(f => f.name === "lastName" )[0].value = this.currentUser.identity.lastName;
				this.formData.fields.filter(f => f.name === "middleName" )[0].value = this.currentUser.identity.middleName;
				this.formData.fields.filter(f => f.name === "firstName" )[0].value = this.currentUser.identity.firstName;
				this.formData.submit.label = "Valider"
			});
    }

    saveIdentity(){
        let identityDto: IdentityDto = {
            civility:this.formData.fields.filter(f => f.name === "civility" )[0].value,
            lastName: this.formData.fields.filter(f => f.name === "lastName" )[0].value,
			      middleName: this.formData.fields.filter(f => f.name === "middleName" )[0].value,
            firstName: this.formData.fields.filter(f => f.name === "firstName" )[0].value};

			this.accountService.saveIdentity(identityDto, this.currentUser.id).then((data: StatusResponseDto) => {
			    if(data.status == "success"){
			    	if(this.viewCtrl.isOverlay){
							this.viewCtrl.dismiss();
							this.modalService.present(MainAddressPage);
						}else{
			    		let notif: NotificationType = {icon: "star", title: "Sauvegarde réussie", message: "Vos informations ont été bien sauvegardé", buttons: [{id: "0", icon: "star", label: "OK", action: this.closeNotif}], backdropDismiss: true};
							this.notification.present(notif);
						}
          }else{
			        console.log("une erreur est survenue lors de l'enregistrement des données");
			        console.log(data.error);
          }
      });
    }

	test(){
		this.viewCtrl.dismiss();
		this.modalService.present(MainAddressPage);
	}

	closeNotif(){
		this.viewCtrl.dismiss();
	}
}


