import {Component} from '@angular/core';

import {Configuration} from '../../config/voj-configuration';
import {FormType} from '../../models/form-type.model';
import {VojPageHeaderType} from '../../models/voj-page-header.model';
import {VojConfiguration} from '../../models/voj-configuration.model';
import {FormDataService} from '../../services/form-data.service';

@Component({
    selector: 'jobs-list-page',
    templateUrl: 'jobs-list.html',
})
export class JobsListPage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    isMobile: boolean = false;
    formData: FormType;

    constructor(public configurationService: Configuration,
                public testFormDataService: FormDataService) {
        this.config = this.configurationService.configuration;
        this.config.isLargeScreen ? this.isMobile = false : this.isMobile = true;

        this.headerData = {
            title: 'My jobs',
            style: 'any',
            actions: [{
                id: 'xdcfgvyhubijnklbhvg',
                label: 'help',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }],
            isCanceled: true
        };
        this.formData = {
            fields: [
                {
                    type: 'customSelect2',
                    value: '',
                    label: 'Test Label',
                    dataset: [
                        { id: 4, key: 'test 1', value: 'test1' },
                        { id: 5, key: 'test 2', value: 'test2' },
                        { id: 6, key: 'test 3', value: 'test3' }
                    ],
                    required: false,
                    visible: true,
                    chosenItems: [],
                    id: 'search-select',
                    name: 'customSelectWithSearch'
                },
            ],
            submit: {
                id: 'xdcfgvyhubijnkfwlbhvg',
                label: 'Validate',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }
        }
    }

    getItems(ev: any) {
        this.formData.fields[0].chosenItems = this.testFormDataService.getData();
        // Reset items back to all of the items

        // set val to the value of the searchbar
        let val = ev.target.value;

        // if the value is an empty string don't filter the items
        if (val && val.trim() != '') {
            this.formData.fields[0].chosenItems = this.formData.fields[0].chosenItems.filter((item) => {
                return (item.toLowerCase().indexOf(val.toLowerCase()) > -1);
            })
        }
    }


    ionViewDidLoad() {
        console.log('ionViewDidLoad BankSettingsPage');
    }

}



