import { Injectable } from '@angular/core';

import { NotificationType } from '../../models/notification-type.model';
import { VojNotificationService } from '../../components/voj-notification/voj-notification.service';
import {AccountService} from "../account-service/account-service";
import {StatusResponseDto} from "../../dtos/status-response.dto";

@Injectable()
export class ResetPasswordService {

    constructor(public vojNotification: VojNotificationService,
                public accountService: AccountService) {}

    present() {
        console.log('Show Reset Password modal.');

        let notificationData: NotificationType = {
            icon: '',
            title: '',
            message: 'RESET_PASSWORD.MESSAGE',
            backdropDismiss: true,
            buttons: [
                {
                    id: 'smsBtn',
                    label: 'RESET_PASSWORD.SMS_BTN',
                    icon: '',
                    action: () => {
                        console.log('SMS was clicked');
                        //this.sendPassword('sms');
                    }
                },
                {
                    id: 'mailBtn',
                    label: 'RESET_PASSWORD.MAIL_BTN',
                    icon: '',
                    action: () => {
                        console.log('Mail was clicked');
                        //this.sendPassword('mail', phone, accountId);
                    }
                }
            ]
        };
        this.vojNotification.present(notificationData);
    }

    sendPassword(canal: string, phone: string, accountId: number){
        this.accountService.sendPassword(phone, "").then((data: StatusResponseDto) => {
            if(data.status == "success"){
							// Present confirmation modal
							this.presentConfirmation(canal);
            }else{
                console.log("Erreur lors de l'envoi du SMS");
            }
        });
    }

    presentConfirmation(method: string) {
        console.log('Show Present Confirmation modal.');

        let notificationData: NotificationType = {
            icon: '',
            title: '',
            message: (method == 'sms' ? 'RESET_PASSWORD.CONFIRMATION_MESSAGE' : 'RESET_PASSWORD.CONFIRMATION_MESSAGE'),
            backdropDismiss: true,
            buttons: [
                {
                    id: 'connectBtn',
                    label: 'RESET_PASSWORD.CONNECT_BTN',
                    icon: '',
                    action: () => {
                        console.log('Connect was clicked');
                    }
                },
                /*{
                    id: 'sendAgainBtn',
                    label: 'RESET_PASSWORD.SEND_AGAIN_BTN',
                    icon: '',
                    action: () => {
                        console.log('Send again was clicked');
                        // Present confirmation modal
                        this.presentConfirmation(method);
                    }
                }*/
            ]
        };

        this.vojNotification.present(notificationData);
    }
}



